.navbar {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 10px;
	border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.logo {
	text-decoration: none;
	color: white;
	font-size: 70px;
	font-family: Brush Script MT;
	letter-spacing: 5px;
	color: rgb(245, 129, 148);
	transition: 1s;
}
.logo:hover {
	letter-spacing: 10px;
	color: pink;
}

.menu-div ul li {
	display: inline-block;
	list-style-type: none;
	padding: 10px;
}

.link {
	text-decoration: none;
	font-size: 20px;
	color: pink;
	transition: 1s;
}
.link:hover {
	color: rgb(245, 129, 148);
}
.active-link {
	border-bottom: 2px solid rgb(245, 129, 148);
}
