/* Home page Style */

.home-div {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: space-evenly;
}

.desc-div {
	width: 50%;
}

.desc-div p {
	color: pink;
	font-size: 30px;
	font-family: Courier New, monospace;
	text-align: left;
}

.btn {
	padding: 10px;
	color: pink;
	border: 2px solid white;
	font-size: 23px;
	border-radius: 5px;
	background-color: rgb(95, 74, 78);
	transition: 2s;
}

.btn:hover {
	letter-spacing: 5px;
	color: rgb(250, 154, 170);
	border: 2px solid green;
}

/* End of Home page Style */

/* Sign up page style */
.signup-div {
	width: 50%;
	margin: 0 auto;
	text-align: center;
	margin-top: 30px;
	border-radius: 20px;
	box-shadow: 2px 2px 20px rgba(102, 102, 101, 0.85);
	padding: 20px;
}

.signup-div h1 {
	color: pink;
	font-size: 50px;
	font-family: Brush Script MT;
}

.signup-input-field {
	padding: 10px;
	font-size: 20px;
	margin: 5px;
	width: 70%;
	border-radius: 10px;
	outline: none;
}

.signup-input-field:hover {
	border: 2px solid green;
}

.signup-btn {
	padding: 10px;
	font-size: 30px;
	margin-top: 5px;
	width: 40%;
	color: white;
	background-color: rgb(29, 146, 29);
	transition: 2s;
}

.signup-btn:hover {
	letter-spacing: 5px;
	background-color: green;
}

/* End of Signup page style */

/* Tutorail page style */

.tutroail-div {
	padding: 10px;
	box-shadow: 2px 2px 20px rgba(102, 102, 101, 0.85);
	margin: 20px;
	border-radius: 10px;
	transition: 2s;
}
.tutroail-div:hover {
	transform: scale(1.05);
}

.tut-title {
	color: rgb(248, 139, 157);
	padding-left: 20px;
	font-family: Brush Script MT;
}
.tut-desc {
	color: rgb(138, 133, 133);
	text-align: center;
	padding: 40px;
	font-size: 20px;
}

/* End of tutoral page style */

/* Media Query */

@media screen and (max-width: 750px) {
	.home-div {
		flex-direction: column;
	}
	.desc-div {
		width: 100%;
	}
	.desc-div p {
		font-size: 20px;
		align-content: center;
	}
	.signup-div {
		width: 90% !important;
	}
}
