.answerFlashCard {
	padding: 3px;
	border-radius: 10px;
	border: 2px solid green;
	color: white;
	background-color: rgb(68, 67, 67);
	transition: 2s;
}
.answerFlashCard:hover {
	background-color: rgb(235, 226, 226);
	color: black;
}

.answerFlashCard p {
	padding: 5px;
	font-family: Cursive;
}
