.qst_div {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-items: center;
	justify-content: space-evenly;
}

.qst_btn {
	padding: 10px;
	border: 0;
	font-size: 11px;
	border-radius: 10px;
	transition: 1s;
}
.qst_btn:hover {
	color: white;
	background-color: rgb(129, 82, 82);
}
