.login-div {
	text-align: center;
	margin-top: 30px;
	border-radius: 20px;
	box-shadow: 2px 2px 20px rgba(102, 102, 101, 0.85);
	padding: 20px;
}

.login-div h1 {
	color: pink;
	font-size: 50px;
	font-family: Brush Script MT;
}

.login-input-field {
	padding: 10px;
	font-size: 20px;
	margin: 5px;
	width: 90%;
	border-radius: 10px;
	outline: none;
}

.login-input-field:hover {
	border: 2px solid green;
}

.login-btn {
	padding: 10px;
	font-size: 30px;
	margin-top: 5px;
	width: 50%;
	color: white;
	background-color: rgb(29, 146, 29);
	transition: 2s;
}

.login-btn:hover {
	letter-spacing: 5px;
	background-color: green;
}
